import AreaChartCard from "./AreaChartCard";
import BarChartCard from "./BarChartCard";

const areaData1 = [
  { month: "Jan", value: 120 },
  { month: "Feb", value: 150 },
  { month: "Mar", value: 170 },
  { month: "Apr", value: 140 },
  { month: "May", value: 200 },
  { month: "Jun", value: 220 },
];
const areaData2 = [
  { month: "Jan", value: 100 },
  { month: "Feb", value: 130 },
  { month: "Mar", value: 160 },
  { month: "Apr", value: 180 },
  { month: "May", value: 210 },
  { month: "Jun", value: 230 },
];
const areaConfig = {
  value: {
    label: "Value",
    color: "var(--primary)",
  },
};

const barData1 = [
  { month: "Jan", value: 80 },
  { month: "Feb", value: 120 },
  { month: "Mar", value: 100 },
  { month: "Apr", value: 140 },
  { month: "May", value: 160 },
  { month: "Jun", value: 180 },
];
const barData2 = [
  { month: "Jan", value: 90 },
  { month: "Feb", value: 110 },
  { month: "Mar", value: 130 },
  { month: "Apr", value: 150 },
  { month: "May", value: 170 },
  { month: "Jun", value: 190 },
];
const barConfig = {
  value: {
    label: "Value",
    color: "var(--primary)",
  },
};

export default function CompareSimulations() {
  return (
    <div className="flex flex-col gap-8 w-full">
      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        <AreaChartCard
          data={areaData1}
          config={areaConfig}
          areaKey="value"
          title="Simulation 1 - Area Chart"
          description="Performance over 6 months"
          summary="+15% growth"
          footerSubtext="Jan - Jun 2024"
        />
        <AreaChartCard
          data={areaData2}
          config={areaConfig}
          areaKey="value"
          title="Simulation 2 - Area Chart"
          description="Performance over 6 months"
          summary="+18% growth"
          footerSubtext="Jan - Jun 2024"
        />
      </div>
      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        <BarChartCard
          data={barData1}
          config={barConfig}
          barKey="value"
          title="Simulation 1 - Bar Chart"
          description="Monthly values"
          summary="Avg: 130"
        />
        <BarChartCard
          data={barData2}
          config={barConfig}
          barKey="value"
          title="Simulation 2 - Bar Chart"
          description="Monthly values"
          summary="Avg: 140"
        />
      </div>
      <div className="mt-8 p-6 rounded-xl bg-muted text-muted-foreground">
        <h3 className="text-lg font-semibold mb-2 text-foreground">Comparison Summary</h3>
        <ul className="list-disc pl-6 space-y-1">
          <li>Simulation 2 shows slightly higher growth in both area and bar charts.</li>
          <li>Average monthly value is higher in Simulation 2 (140 vs 130).</li>
          <li>Both simulations show positive trends, but Simulation 2 is more consistent.</li>
        </ul>
      </div>
    </div>
  );
} 